
import { defineComponent, PropType } from "vue";
import { prefix, oneOf } from "@/utils/assist";

interface ColorProp {
    line?: string;
    background?: string;
    font?: string;
}

interface Color {
    borderLeftColor?: string;
    backgroundColor?: string;
    color?: string;
}

export default defineComponent({
    name: `${prefix}Alert`,
    props: {
        type: {
            type: String,
            validator(value: string) {
                return oneOf(value, ["success", "danger", "warning", "info"]);
            },
        },
        color: {
            type: Object as PropType<ColorProp>,
        },
    },
    computed: {
        getType(): string {
            if (this.type) {
                return `is-${this.type}`;
            } else {
                return "";
            }
        },
        getColor(): Color {
            if (this.color) {
                const style: Color = {};
                if (this.color.line) {
                    style["borderLeftColor"] = this.color.line;
                }
                if (this.color.background) {
                    style["backgroundColor"] = this.color.background;
                }
                if (this.color.font) {
                    style["color"] = this.color.font;
                }
                return style;
            } else {
                return {};
            }
        },
    },
});
