
import { computed, defineComponent, reactive, toRefs, watchEffect } from "vue";
import { getScrollWidth, prefix } from "@/utils/assist";

export default defineComponent({
    name: `${prefix}Drawer`,
    props: {
        visiable: {
            type: Boolean,
            default: false,
        },
        width: {
            type: String,
            default: "300px",
        },
        styles: {
            type: Object,
            default: () => {
                return {
                    padding: "16px",
                };
            },
        },
        maskClosable: Boolean,
    },
    setup(props, { emit }) {
        const scrollbarWidth = getScrollWidth();
        const data = reactive({
            state: props.visiable,
        });
        const hideDrawer = () => {
            if (!props.maskClosable) emit("update:visiable", false);
        };
        watchEffect(() => {
            data.state = props.visiable;
        });
        const position = computed(() => {
            const style = {};
            if (data.state) {
                // style = { right: 0 };
                document.body.style.paddingRight = `${scrollbarWidth}px`;
                document.body.style.overflow = "hidden";
            } else {
                // style = { right: `${-props.width}px` };
                document.body.setAttribute("style", "");
            }
            return style;
        });

        return {
            hideDrawer,
            ...toRefs(data),
            position,
        };
    },
});
