
import { defineComponent } from "vue";
import { prefix } from "@/utils/assist";

export default defineComponent({
    name: `${prefix}Aside`,
    props: {
        width: {
            type: String,
            default: "300px",
        },
    },
    computed: {
        getWidth(): string {
            return `width:${this.width};`;
        },
    },
});
