
import { defineComponent, inject } from "vue";
import { prefix } from "@/utils/assist";

export default defineComponent({
    name: `${prefix}Footer`,
    props: {
        height: {
            type: String,
            default: "60px",
        },
    },
    setup() {
        const layout: any = inject("layout");

        return {
            layout,
        };
    },
    mounted() {
        this.layout.updateNames(this.$options.name);
    },
    computed: {
        getHeight(): string {
            return `height:${this.height};`;
        },
    },
});
