
import { defineComponent } from "vue";
import { prefix } from "@/utils/assist";

export default defineComponent({
    name: `${prefix}BreadcrumbItem`,
    inject: ["separator"],
    props: {
        to: {
            type: [String, Object],
        },
        append: {
            type: Boolean,
            default: false,
        },
        replace: {
            type: Boolean,
            default: false,
        },
        target: {
            type: String,
            default: "_self",
        },
    },
    computed: {
        isCom() {
            if (this.to) {
                return "routerLink";
            } else {
                return "span";
            }
        },
        tags() {
            let tag = "span";
            if (this.to) {
                tag = "a";
            }
            return tag;
        },
    },
});
