
import { prefix } from "@/utils/assist";
import { defineComponent } from "vue";

export default defineComponent({
    name: `${prefix}Cell`,
    props: {
        title: String,
        name: [String, Number],
        to: [String, Object],
        replace: {
            type: Boolean,
            default: false,
        },
        icon: String,
        label: String,
        extra: String,
        isLink: {
            type: Boolean,
            default: false,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        selected: {
            type: Boolean,
            default: false,
        },
        target: {
            type: String,
            default: "_self",
        },
    },
    setup(props, { slots }) {
        const showIcon = () => {
            return !!props.icon || (slots && slots.icon);
        };
        const showLabel = () => {
            return !!props.label || (slots && slots.label);
        };
        const showArrow = () => {
            return (slots && !!slots.arrow) || props.isLink || !!props.to;
        };
        const extraStyle = () => {
            const right = {
                right: "34px",
            };
            const flag = !!props.to || props.isLink || (slots && slots.arrow);
            return flag ? right : {};
        };
        return { showIcon, showLabel, extraStyle, showArrow };
    },
    methods: {
        handleClick() {
            this.$emit("on-click", this.name);
        },
    },
});
